'use client';
import { VaccinationTable, Vaccination } from '@/components/VaccinationTable';
import AddVaccinationForm from '@/components/AddVaccinationForm';
import { useEffect, useState } from 'react';

export default function Home() {
  const [vaccinations, setVaccinations] = useState<Vaccination[]>([]);
  const [showForm, setShowForm] = useState(false);
  const [loading, setLoading] = useState(true);

  const fetchVaccinations = async () => {
    setLoading(true);
    const res = await fetch('/api/vaccinations');
    const data = await res.json();
    setVaccinations(data);
    setLoading(false);
  };

  useEffect(() => {
    fetchVaccinations();
  }, []);

  const handleMarkComplete = async (id: string) => {
    const vac = vaccinations.find(v => v.id === id);
    if (!vac) return;
    const today = new Date();
    // Next due date: 1 year from today
    const nextDue = new Date(today);
    nextDue.setFullYear(today.getFullYear() + 1);
    await fetch('/api/vaccinations', {
      method: 'PATCH',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        id,
        lastCompleted: today.toISOString(),
        dueDate: nextDue.toISOString(),
      }),
    });
    fetchVaccinations();
  };

  return (
    <div className="max-w-3xl mx-auto p-6">
      <div className="flex justify-between items-center mb-6">
        <div>
          <h1 className="text-3xl font-bold mb-1">Bobby's vaccinations</h1>
          <p className="text-lg text-gray-600">Golden Retriever | 1 year old</p>
        </div>
        <button
          className="bg-[#144C4C] text-white px-4 py-2 rounded-full"
          onClick={() => setShowForm(v => !v)}
        >
          {showForm ? 'CLOSE' : 'ADD VACCINATION'}
        </button>
      </div>
      {showForm && (
        <div className="mb-6">
          <AddVaccinationForm onAdd={() => { setShowForm(false); fetchVaccinations(); }} />
        </div>
      )}
      {loading ? (
        <div className="text-center py-10">Loading...</div>
      ) : (
        <VaccinationTable vaccinations={vaccinations} onMarkComplete={handleMarkComplete} />
      )}
    </div>
  );
}
