'use client';
import React from 'react';
import { StatusBadge } from './StatusBadge';
import { format } from 'date-fns';

export type Vaccination = {
  id: string;
  name: string;
  lastCompleted: string | null;
  dueDate: string;
  createdAt: string;
  updatedAt: string;
};

type Props = {
  vaccinations: Vaccination[];
  onMarkComplete: (id: string) => void;
};

function getStatus(dueDate: Date): 'completed' | 'due soon' | 'over due' {
  const today = new Date();
  const diff = (dueDate.getTime() - today.getTime()) / (1000 * 60 * 60 * 24);
  if (diff < 0) return 'over due';
  if (diff <= 30) return 'due soon';
  return 'completed';
}

export function VaccinationTable({ vaccinations, onMarkComplete }: Props) {
  return (
    <table className="min-w-full border mt-6 bg-white">
      <thead>
        <tr className="bg-gray-100">
          <th className="py-2 px-4 text-left">Name</th>
          <th className="py-2 px-4 text-left">Status</th>
          <th className="py-2 px-4 text-left">Last Completed</th>
          <th className="py-2 px-4 text-left">Due Date</th>
          <th className="py-2 px-4 text-left">Action</th>
        </tr>
      </thead>
      <tbody>
        {vaccinations.map(vac => {
          const status = getStatus(new Date(vac.dueDate));
          return (
            <tr key={vac.id} className="border-t">
              <td className="py-2 px-4">{vac.name}</td>
              <td className="py-2 px-4"><StatusBadge status={status} /></td>
              <td className="py-2 px-4">{vac.lastCompleted ? format(new Date(vac.lastCompleted), 'yyyy-MM-dd') : '-'}</td>
              <td className="py-2 px-4">{format(new Date(vac.dueDate), 'yyyy-MM-dd')}</td>
              <td className="py-2 px-4">
                <button
                  className="bg-[#144C4C] text-white px-4 py-2 rounded-full"
                  onClick={() => onMarkComplete(vac.id)}
                  disabled={status === 'completed'}
                >
                  MARK COMPLETE
                </button>
              </td>
            </tr>
          );
        })}
      </tbody>
    </table>
  );
} 